import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";

const DashboardDoctors = () => {
  const navigate = useNavigate();
  const [doctorList, setDoctorList] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [name, setName] = useState("");
  const doctorsPerPage = 7;

  const indexOfLastDoctor = currentPage * doctorsPerPage;
  const indexOfFirstDoctor = indexOfLastDoctor - doctorsPerPage;
  const currentDoctors = doctorList.slice(indexOfFirstDoctor, indexOfLastDoctor);
  const totalPages = Math.ceil(doctorList.length / doctorsPerPage);

  const fetchSearchedDoctors = async (query) => {
    try {
      const centreId = localStorage.getItem("centreId");
      if (!centreId) return;

      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/doctors/searchDoctor?query=${query}&centreId=${centreId}`
      );
      setDoctorList(res.data.data);
    } catch (error) {
      console.error("Error fetching users:", error);
      setDoctorList([]);
    }
  };

  const fetchDoctors = async () => {
    const centreId = localStorage.getItem("centreId");
    if (!centreId) return;

    try {
      const response = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/doctors/centre/${centreId}`
      );
      setDoctorList(response.data || []);
    } catch (error) {
      console.error("Error fetching doctors:", error);
    }
  };

  useEffect(() => {
    const delayDebounce = setTimeout(() => {
      if (name.trim() !== "") {
        fetchSearchedDoctors(name);
      } else {
        fetchDoctors();
      }
    }, 500);

    return () => clearTimeout(delayDebounce);
  }, [name]);

  useEffect(() => {
    fetchDoctors();
  }, []);

  const handleDelete = async (doctorId) => {
    const confirmDelete = window.confirm(
      "Are you sure you want to delete this doctor?"
    );
    if (!confirmDelete) return;

    try {
      await axios.delete(
        `${process.env.REACT_APP_BACKEND_URL}/api/doctors/${doctorId}`
      );
      toast.success("Doctor deleted successfully!");
      setDoctorList((prev) => prev.filter((doctor) => doctor._id !== doctorId));
    } catch (error) {
      console.error("Failed to delete doctor:", error);
      toast.error("Failed to delete doctor. Please try again.");
    }
  };

  const handleEdit = (id) => {
    navigate(`/edit-doctor/${id}`);
  };

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };
  
  const handlePageChangeWithScroll = (page) => {
  handlePageChange(page); // your existing page change logic
  window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
};

  const renderPagination = () => {
    const pages = [];
    const maxVisible = 4;
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = startPage + maxVisible - 1;

    if (endPage > totalPages) {
      endPage = totalPages;
      startPage = Math.max(1, endPage - maxVisible + 1);
    }
 pages.push(
    <li
      key="first"
      className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
    >
      <button
        className="page-link"
        onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
       
      >
        First
      </button>
    </li>
  );


    pages.push(
      <li
        key="prev"
        className={`page-item ${currentPage === 1 ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)}
        >
          Prev
        </button>
      </li>
    );

    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li
          key={i}
          className={`page-item ${currentPage === i ? "active" : ""}`}
        >
          <button className="page-link" onClick={() =>  handlePageChangeWithScroll(i)}>
            {i}
          </button>
        </li>
      );
    }

    pages.push(
      <li
        key="next"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage < totalPages && handlePageChangeWithScroll(currentPage + 1)
          }
        >
          Next
        </button>
      </li>
    );

    pages.push(
      <li
        key="last"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => handlePageChangeWithScroll(totalPages)}
        >
          Last
        </button>
      </li>
    );

    return pages;
  };

  return (
    <div className="dashboard-body__content">
      <div className="row gy-4">
        <div className="col-12">
          <div className="card common-card border border-gray-five">
            <div className="card-body">
              {/* Header Section */}
              <div className="doctor-header">
                <h4 className="doctor-title">Available Doctors</h4>
                <div className="doctor-actions">
                  <input
                    type="text"
                    className="common-input border-gray-five doctor-search-input"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    placeholder="Search by name or specialization"
                  />
                  <button
                    className="add-doctor-btn"
                    onClick={() => navigate("/create-doctor")}
                  >
                    Add Doctors
                  </button>
                </div>
              </div>

              <div className="table-responsive">
                <table className="table text-body mt--24">
                  <thead>
                    <tr>
                      <th>SN.</th>
                      <th>Image</th>
                      <th>Name</th>
                      <th>Specialization</th>
                      <th>Qualification</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentDoctors.length > 0 ? (
                      currentDoctors.map((doc, i) => (
                        <tr key={doc._id}>
                          <td>{indexOfFirstDoctor + i + 1}</td>
                          <td data-label="Image">
                            <img
                              src={doc.profilePhoto}
                              alt="Doctor Avatar"
                              className="doctor-avatar"
                            />
                          </td>
                          <td data-label="Name">{doc.name}</td>
                          <td data-label="Specialization">
                            {doc.specialization}
                          </td>
                          <td data-label="Qualification">
                            {doc.qualification?.length > 70
                              ? doc.qualification.slice(0, 70) + "..."
                              : doc.qualification}
                          </td>
                          <td>
                            <button
                              className="edit-btn"
                              onClick={() => handleEdit(doc._id)}
                            >
                              Edit
                            </button>
                          </td>
                        </tr>
                      ))
                    ) : (
                      <tr>
                        <td colSpan="6" className="text-center">
                          No doctors found.
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>

              {totalPages > 1 && (
                <div className="flx-between gap-2">
                  <div className="paginate-content flx-align flex-nowrap gap-3">
                    <span className="paginate-content__text fs-14">
                      Showing {indexOfFirstDoctor + 1} –{" "}
                      {Math.min(indexOfLastDoctor, doctorList.length)} of{" "}
                      {doctorList.length}
                    </span>
                  </div>
                  <nav aria-label="Doctor pagination">
                    <ul className="pagination justify-content-center mt-0">
                      {renderPagination()}
                    </ul>
                  </nav>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      <style>
        {`
          .doctor-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-wrap: wrap;
            gap: 15px;
            margin-bottom: 20px;
          }

          .doctor-actions {
            display: flex;
            gap: 15px;
            align-items: center;
          }

          .doctor-search-input {
            max-width: 255px;
          }

          .add-doctor-btn {
            background-color: green;
            color: whitesmoke;
            padding: 10px 15px;
            border-radius: 5px;
            font-weight: 600;
            border: none;
            cursor: pointer;
            white-space: nowrap;
          }

          .doctor-avatar {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            object-fit: cover;
          }

          .edit-btn {
            background-color: #007bff;
            color: white;
            border: none;
            border-radius: 4px;
            padding: 5px 10px;
            margin-right: 5px;
            cursor: pointer;
          }

          /* Mobile Responsive Styles */
          @media (max-width: 667px) {
            .doctor-header {
              flex-direction: column;
              align-items: flex-start;
              gap: 15px;
            }

            .doctor-title {
              text-align: center;
              margin-bottom: 0;
              order: 1;
            }

            .doctor-actions {
              flex-direction: column;
              gap: 10px;
              width: 100%;
              order: 2;
            }

            .doctor-search-input {
              max-width: 100% !important;
              width: 100%;
              order: 2;
            }

            .add-doctor-btn {
              width: 100%;
              padding: 12px;
              order: 3;
            }
          }

          /* Small mobile devices */
          @media (max-width: 480px) {
            .doctor-header {
              gap: 10px;
            }

            .table-responsive {
              font-size: 12px;
            }

            .doctor-avatar {
              width: 40px;
              height: 40px;
            }

            .edit-btn {
              padding: 4px 8px;
              font-size: 12px;
            }

            .paginate-content__text {
              font-size: 12px;
            }

            .page-link {
              padding: 6px 10px;
              font-size: 12px;
            }
          }
        `}
      </style>

      <ToastContainer position="top-right" autoClose={3000} />
    </div>
  );
};

export default DashboardDoctors;